@tailwind base;
@tailwind components;
@tailwind utilities;


@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Noto+Sans:wght@100;200;300;400;500;600;700;800&family=Poppins:wght@300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;700&display=swap');

body{
  background:rgba(21, 54, 129, 0.4)
}


.font-montserrat{
  font-family:montserrat
}



.open{
  display:none;
}

#myList.active{
  transform:translateX(0)
}

.modal.active{

}


* {
  box-sizing: border-box;
  font-family:montserrat;
}


#myList.active{
  box-shadow:-12px 10px 71px -11px rgba(0,0,0,0.75)
}

#myList ul li{
display:block;
padding:10px 0;
}

#myList ul li .delete-btn{
display:inline-block;
background:red;
padding:10px;
color:white;
margin-left:20px;
border-radius:5px;
cursor:pointer;
}

#myList .close-list{
  display:inline-block;
  margin:0 auto;
  background:black;
  color:white;
  padding:20px 40px;
  text-align:center;
  cursor:pointer;

}




.modal-background.active{
  visibility:visible
}

 
.modal{
  top:50%;
  left:50%;
  transform:translate(-50%,-50%) scale(0);
}
.modal.active{
  transform:translate(-50%, -50%) scale(1);

  /* transform:scale(1) translateX(-50%) translateY(-50%) ; */
}

.close-button:hover .x-mark{
transform:scale(1.2);
}

